'use strict';

import TimeCodeService from './med.time.code.srv.js';

export default class RejectionService extends TimeCodeService {

  /*@ngInject*/
  constructor(timeBaseTransformerService) {
    super(timeBaseTransformerService);
    this.timeBaseTransformer = timeBaseTransformerService;
  }

  updateTCLen(rejection, frames){
    if(this._areFormatsValid(rejection) && this.isTCInVsTCOutValid(rejection)) {
      rejection.tcLenStr = this._minus(rejection.tCOutStr,rejection.tCInStr,frames);
    }
  }

  areFramesValid(rejections, frames) {
    return typeof rejections.find(rej => !this._areFramesValid(rej, frames)) === 'undefined';
  }

  _areFramesValid(rejection, frames){
    let toValidate = [rejection.tCInStr,rejection.tcLenStr];
    
    return typeof toValidate.find(it => !this.isFramesValid(it, frames)) === 'undefined';
  }

  areFormatValid(rejections) {
    return typeof rejections.find(rej => !this._areFormatsValid(rej)) === 'undefined';
  }

  _areFormatsValid(rejection){
    let toValidate = [rejection.tCInStr,rejection.tCOutStr];
    
    return typeof toValidate.find(it => !this.isFormatValid(it)) === 'undefined';
  }
  
  isInsideMaterial(tc,tcMin,tcMax){
    return( this._isBiggerOrEqual(tc, tcMin) &&
            this._isBiggerOrEqual(tcMax, tc)
    );
  }

  areInsideMaterial(rejections,tcMin,tcMax){
    return typeof rejections.find(rej => !this.isInsideMaterial(rej.tCInStr, tcMin, tcMax) || !this.isInsideMaterial(rej.tCOutStr, tcMin, tcMax)) === 'undefined';
  }

  isTCInVsTCOutValid(rejection){
    return this._isBiggerOrEqual(rejection.tCOutStr,rejection.tCInStr);
  }

  areTCInVsTCOutValid(rejections){
    return typeof rejections.find(rej => !this.isTCInVsTCOutValid(rej)) === 'undefined';
  }
  
}
